* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  height: 100vh;
  background: #f0f8ff;
  color: #333;
  font-family: "Courier New", Courier, monospace;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.container {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.form {
  position: absolute;
  background-color: rgba(255, 255, 255, 0.8);
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  width: 300px;
  margin-bottom: 20px;
}

h2 {
  color: #000080;
}

.input {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

.button {
  width: 100%;
  padding: 10px;
  background-color: #000080;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
}

.button:hover {
  background-color: #333;
}

.link {
  position: fixed;
  bottom: 20px;
  left: 20px;
  font-size: 20px;
  color: #000080;
  text-decoration: none;
  background: #e6e6fa;
  padding: 10px 15px;
  border-radius: 5px;
}

.link:hover {
  background: #dcdcdc;
}

.code {
  position: absolute;
  font-size: 16px;
  opacity: 0.8;
  animation: floatCode 8s infinite ease-in-out;
  white-space: pre;
  color: #000080;
}

@keyframes floatCode {
  0% {
    opacity: 0;
    transform: translateY(100vh) scale(0.8);
  }
  50% {
    opacity: 1;
    transform: translateY(50vh) scale(1);
  }
  100% {
    opacity: 0;
    transform: translateY(-50vh) scale(0.8);
  }
}
